import "./About.css";
import { MovingButton } from "../Button/Button";
import React from 'react';

import star from '../../images/star.png';
import quote from '../../images/quote.png';
import agency1 from '../../images/agency1.png';
import agency2 from '../../images/agency2.png';
import avatar from '../../images/avatar.png';

const About = () => {
  return (
    <section className="aboutAgency">
      <div className="container-fluid">
        <div className="row px-5 align-end">
          <div className="col md-7">
            <div className="agencySide position-relative">
              <img
                className="position-absolute start-0"
                src={quote}
                alt="Quote"
              />
              <img
                className="position-absolute start-0"
                src={agency2}
                alt="Agency 2"
              />
              <img src={avatar} className="aboutAvatar" alt="Avatar" />
              <img
                className="position-absolute end-0 pe-5"
                src={agency1}
                alt="Agency 1"
              />
            </div>
          </div>
          <div className="col md-5 pb-5 mb-3">
            <div className="tagline">
              <img src={star} alt="Star" />
              <p>Built to Make Them Call</p>
            </div>
            <h2 className="yoso-h2">Your Free Website Is Just the Start</h2>
            <p className="yoso-desc">
              They say there’s no free lunch—but we’ll build your contractor website free, because we know you’ll want seconds.
            </p>
            <ul className="list-unstyled yoso-list">
              <li>
                <i className="fa-solid fa-circle-check"></i>70% Upgrade or Refer
              </li>
              <li>
                <p className="yoso-desc">
                  Most clients join a marketing plan or refer someone who does.
                </p>
              </li>
              <li>
                <i className="fa-solid fa-circle-check"></i>Call-Driving Design That Works
              </li>
              <p className="yoso-desc">
                Look legit, rank locally, and turn visitors into real phone calls.
              </p>
            </ul>
            <MovingButton label="read more" href="#" classes="yoso-button mt-5" icon="fa-arrow-right" />
          </div>
        </div>
      </div>
    </section>
  );
};

export default About;
