import "./Hero.css";
import { MovingButton } from "../Button/Button";
import RequestForm from "./RequestForm";
import React from 'react';
import star from '../../images/star.png'; // ✅ Correct image import

const Hero = () => {
  return (
    <section className="hero">
      <div className="row">
        <div className="col md-6">
          <div className="tagline">
            <img src={star} alt="Star" />
            <p>With SEO Optimization</p>
          </div>
          <h1 className="yoso-h1">
            {/* Make Them Call You. */}
            Grow Your Business
          </h1>
          <p className="yoso-desc">
            Professionally built—just cover hosting ($14.99/mo).
            Claim your website before time runs out!
          </p>
          <div className="row">
            <div className="col md-6 tab-100">
              <MovingButton
                label="Read More"
                href="#"
                classes="yoso-button"
                icon="fa-arrow-right"
                movingEffect={true}
              />
            </div>
            <div className="col md-6 tab-100">
              <ul className="list-unstyled yoso-list">
                <li>
                  <i className="fa-solid fa-circle-check"></i>Custom Design for Contractors
                </li>
                <li>
                  <i className="fa-solid fa-circle-check"></i>Easy Local Lead Generation
                </li>
              </ul>
            </div>
          </div>
        </div>
        <div className="col md-5 offset-md-1">
          <RequestForm />
        </div>
      </div>
    </section>
  );
};

export default Hero;
