import React, { useRef, useEffect } from "react";
import { useKeenSlider } from "keen-slider/react";
import "keen-slider/keen-slider.min.css";
import "./Gallery.css";
import type { SliderItem } from "../../types/SliderItems";

import page1 from "../../page-screenshots/site-01.avif";
import page2 from "../../page-screenshots/site-02.avif";
import page3 from "../../page-screenshots/site-03.avif";
import page4 from "../../page-screenshots/site-04.avif";
import star from "../../images/star.png";

const Gallery: React.FC = () => {
  const animationRef = useRef(0);
  const lastTimeRef = useRef<number>(0);

  const [sliderRef, instanceRef] = useKeenSlider<HTMLDivElement>({
    loop: true,
    renderMode: "performance",
    slides: {
      origin: "center",
      perView: 3,
      spacing: 20,
    },
    breakpoints: {
      "(max-width: 768px)": {
        slides: { perView: 1 },
      },
      "(max-width: 1024px)": {
        slides: { perView: 2 },
      },
    },
    created() {
      lastTimeRef.current = performance.now();
      requestAnimationFrame(animate);
    },
  });

  const speed = 0.1;

  function animate(time: number) {
    if (instanceRef.current) {
      const delta = time - lastTimeRef.current;
      lastTimeRef.current = time;
      instanceRef.current.moveToIdx(
        instanceRef.current.track.details.abs + (speed * delta) / 10,
        true
      );
    }
    animationRef.current = requestAnimationFrame(animate);
  }

  useEffect(() => {
    return () => cancelAnimationFrame(animationRef.current);
  }, []);

  const sliderItems: SliderItem[] = [
    { label: "Click to see", title: "Painters", link: "https://www.idealfloorsidaho.com/", src: page1 },
    { label: "Click to see", title: "Concrete", link: "https://www.idealfloorsidaho.com/", src: page2 },
    { label: "Click to see", title: "Drywall", link: "https://www.idealfloorsidaho.com/", src: page3 },
    { label: "Click to see", title: "Flooring", link: "https://www.idealfloorsidaho.com/", src: page4 },
    // { label: "Click to see", title: "Cabinets", link: "https://www.idealfloorsidaho.com/", src: page1 },
    // { label: "Click to see", title: "Roofing", link: "https://www.idealfloorsidaho.com/", src: page3 },
  ];

  return (
    <section id="gallery" className="yoso-margin yoso-gallery">
      <div className="tagline justify-center">
        <img src={star} alt="Star" />
        <p>See Real Sites</p>
      </div>
      <h2 className="text-center yoso-h2">
        Preview examples of free contractor websites we’ve built.
      </h2>

      <div ref={sliderRef} className="keen-slider mt-5">
        {sliderItems.map((item, index) => (
          <div className="keen-slider__slide" key={index}>
            <div className="gallerySingle">
              <img
                className="w-100 rounded-3"
                style={{ border: "1px solid rgba(0,0,0,0.15)" }}
                src={item.src}
                alt={item.title}
              />
              <div className="aboutGallery">
                <article>
                  <h6 className="yoso-h6 m-0 p-0 mb-2">{item.title}</h6>
                  <h4 className="yoso-h4 m-0 p-0">{item.label}</h4>
                </article>
                <a
                  href={item.link}
                  className="yoso-button-3"
                  target="_blank"
                  rel="noreferrer"
                >
                  <span>
                    <i className="fa-solid fa-arrow-right"></i>
                    <i className="fa-solid fa-arrow-right"></i>
                  </span>
                </a>
              </div>
            </div>
          </div>
        ))}
      </div>
    </section>
  );
};

export default Gallery;
