import "./Pricing.css";
import type { PricingItemType } from "../../types/PricingProps";
import { MovingButton } from "../Button/Button";
import React from 'react'

const Pricing: React.FC = () => {
  const pricingItems: PricingItemType[] = [
    {
      Price: "200",
      itemImg: "./images/pricing/price1.png",
      itemTitle: "Local SEO Boost",
      itemDesc: "Be the first result in your zip code",
      features: [
        "Hand-crafted service area pages",
        "Keyword targeting for your trade",
        "Google My Business profile setup",
      ],
      btnTitle: "Boost My Local Ranking"
    },
    {
      Price: "300",
      itemImg: "./images/pricing/price2.png",
      itemTitle: "Ad Campaign Management",
      itemDesc: "More calls, less guesswork",
      features: [
        "Google & Facebook ad setup",
        "Targeted ads to local buyers",
        "Campaign optimization monthly",
      ],
      btnTitle: "Launch My Ads"
    },
    {
      Price: "400",
      itemImg: "./images/pricing/price3.png",
      itemTitle: "SEO + Ads Bundle",
      itemDesc: "Best value, most calls",
      features: [
        "All SEO Boost features",
        "All Ad Management features",
        "Priority support & faster setup",
      ],
      btnTitle: "Go All-In"
    },
  ]

  return (
    <section id="pricing" className="yoso-price yoso-margin">
      <div className="container">
        <div className="tagline justify-center">
          <img src="../../images/star.png" alt="Star" />
          <p>With SEO Optimization</p>
        </div>
        <h2 className="yoso-h2 text-center">
          Pricing Solutions<br />
          for success
        </h2>
        <div className="row mt-5">
          {pricingItems.map((item, index) => (
            <div className="col md-4 tab-50 sm-100" key={index}>
              <div className="priceSingle">
                <div className="price"><sup>$</sup>{item.Price}<span> /month</span></div>
                <img
                  src={item.itemImg}
                  alt={item.itemTitle}
                  className="priceImg"
                />
                <h4 className="yoso-h4">{item.itemTitle}</h4>
                <p className="yoso-desc mt-2 p-0">{item.itemDesc}</p>
                <div className="priceLine"></div>
                <ul className="list-unstyled yoso-list">
                  {item.features.map((feature, i) => (
                    <li key={i}>
                      <i className="fa-solid fa-circle-check"></i>{feature}
                    </li>
                  ))}
                </ul>
                <MovingButton href="#" label={item.btnTitle} classes="yoso-button-4" />
              </div>
            </div>
          ))}
        </div>
        {/* <p className="yoso-desc">
          Want to see our Recent News & Updates.
          <a href="#">Click here to View More</a>
        </p> */}
      </div>
    </section>
  );
}
export default Pricing;