import { MovingButton } from "../Button/Button";
import "./RequestForm.css";
import React from 'react'

const RequestForm = () => {
  return (
    <form className="heroForm">
      <article className="formContent">
        <h2 className="yoso-h2">Claim Your Free Website</h2>
        <p className="yoso-desc">Spots Are Limited!</p>
      </article>
      <div className="inputField">
        <input
          className="yoso-desc"
          type="text"
          name="name"
          id="name"
          placeholder="Your Name"
        />
        <i className="fa-solid fa-user"></i>
      </div>
      <div className="inputField">
        <input
          className="yoso-desc"
          type="email"
          name="email"
          id="email"
          placeholder="Email Address"
        />
        <i className="fa-solid fa-envelope"></i>
      </div>
      <div className="inputField">
        <input
          className="yoso-desc"
          type="tel"
          name="phone"
          id="phone"
          placeholder="Phone Number"
        />
        <i className="fa-solid fa-phone"></i>
      </div>
      {/* <div className="border-bottom pt-4 mb-4"></div>
      <h4 className="yoso-h4">Preferred Consult Method:</h4>
      <div className="radioField-wrapper d-flex flex-wrap">
        <div className="radioField">
          <input type="radio" name="op1" value="Phone Call" />
          <label className="starat-desc">Phone Call</label>
        </div>
        <div className="radioField">
          <input type="radio" name="op1" value="Video Call" />
          <label className="starat-desc">Video Call</label>
        </div>
      </div> */}
      <MovingButton label="Submit Request" classes="yoso-form-button" />
    </form>
  );
}
export default RequestForm;