import "./Loader.css"
import React, { useEffect, useState } from "react";

type LoederProps = {
  label: string;
};

const Loader: React.FC<LoederProps> = ({ label }) => {
  const [hide, setHide] = useState(false);
  const [animateOut, setAnimateOut] = useState(false);

  useEffect(() => {
    const loaderDelay = 3500;

    const timer1 = setTimeout(() => {
      setAnimateOut(true);

      const timer2 = setTimeout(() => {
        setHide(true);
      }, 500);
      return () => clearTimeout(timer2);
    }, loaderDelay);

    return () => clearTimeout(timer1);
  }, []);

  if (hide) return null;

  return (
    <div className={`loader ${animateOut ? "loader--hidden" : ""}`}>
      <div className="loaderTop">
        <span className="loaderTopTxt"> <span className="animateTxt">{label}</span></span>
      </div>
      <div className="loaderBottom">
        <span className="loaderBottomTxt">
          <span className="animateTxt">{label}</span>
        </span>
      </div>
    </div>
  );
}
export default Loader;