import "./Nav.css";
import React, { useState, useRef, useEffect } from "react";
import type { NavProps } from "../../types/NavProps";
import type { NavItem } from "../../types/NavItems";
import icon from '../../images/header/icon.png'; // ✅ Proper import for logo

const Nav: React.FC<NavProps> = ({
  firstMessage,
  secondMessage,
  phone,
  email,
}) => {
  const [isSearchVisible, setIsSearchVisible] = useState(false);
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);

  const navbarRef = useRef<HTMLDivElement | null>(null);

  const handleSearchClick = () => {
    setIsSearchVisible(true);
  };

  const handleCloseSearch = () => {
    setIsSearchVisible(false);
  };

  const toggleMobileMenu = () => {
    setIsMobileMenuOpen(prev => !prev);
  };

  const navItems: NavItem[] = [
    { label: "Home", link: "#home" },
    { label: "Services", link: "#services" },
    { label: "Gallery", link: "#gallery" },
    // { label: "Pricing", link: "#pricing" },
    // { label: "Reviews", link: "#reviews" },
  ];

  useEffect(() => {
    const handleScroll = () => {
      if (navbarRef.current) {
        if (window.scrollY > 750) {
          navbarRef.current.classList.add("sticky");
        } else {
          navbarRef.current.classList.remove("sticky");
        }
      }
    };

    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  return (
    <nav className="nav">
      <div className="topBar desktopTopBar">
        <h5 className="discount">
          <strong>{firstMessage}</strong>: {secondMessage}
        </h5>
        <ul className="list-unstyled">
          <li>
            <i className="fa-solid fa-phone"></i>{phone}
          </li>
          <li>
            <i className="fa-solid fa-envelope"></i>{email}
          </li>
        </ul>
      </div>

      <div className="yoso-menu" id="navbar" ref={navbarRef}>
        <div className="logo">
          <img src={icon} alt="Logo" />MTC
        </div>

        <div className="desktopMenu mainMenu">
          {navItems.map(({ label, link }, key) => (
            <li key={key}>
              <a href={link} className="menuText">
                <span className="menuTextInner">{label}</span>
                <span className="duplicateText">{label}</span>
              </a>
            </li>
          ))}
        </div>

        <div className="mobileMenu">
          <div className={`hamburger ${isMobileMenuOpen ? "is-active" : ""}`} onClick={toggleMobileMenu}>
            <span className="hamburger-line"></span>
            <span className="hamburger-line"></span>
            <span className="hamburger-line"></span>
          </div>

          <ul className={`list-unstyled mainMenu ${isMobileMenuOpen ? "" : "d-none"}`}>
            <div className="topBar">
              <h5 className="discount">
                <strong>{firstMessage}</strong>: {secondMessage}
              </h5>
              <ul className="list-unstyled">
                <li>
                  <i className="fa-solid fa-phone"></i>{phone}
                </li>
                <li>
                  <i className="fa-solid fa-envelope"></i>{email}
                </li>
              </ul>
            </div>

            <div className={`hamburger ${isMobileMenuOpen ? "is-active" : ""}`} onClick={toggleMobileMenu}>
              <span className="hamburger-line"></span>
              <span className="hamburger-line"></span>
              <span className="hamburger-line"></span>
            </div>

            {navItems.map(({ label, link }, key) => (
              <li key={key}>
                <a className="menuText" href={link} onClick={toggleMobileMenu}>
                  <span className="menuTextInner">{label}</span>
                  <span className="duplicateText">{label}</span>
                </a>
              </li>
            ))}

            <li className="dropdown">
              <a href="#blog" className="menuText">
                <span className="menuTextInner">Blog</span>
                <span className="duplicateText"></span>
              </a>
            </li>
          </ul>
        </div>

        <div className="search revealSearch" onClick={handleSearchClick}>
          <i className="fa-solid fa-magnifying-glass"></i>
        </div>

        {isSearchVisible && (
          <div className="searchPopUp" style={{ display: "flex" }}>
            <div className="close search" onClick={handleCloseSearch}>
              <i className="revealSearch fa-solid fa-xmark"></i>
            </div>
            <form>
              <input type="text" placeholder="Type to Search.." />
              <button>
                <i className="fa-solid fa-magnifying-glass"></i>
              </button>
            </form>
          </div>
        )}
      </div>
    </nav>
  );
};

export default Nav;
