import React from 'react';
import { MovingButton } from "../Button/Button";
import "./EvaluationItems.css";

import announcement from "../../images/announcement.png"; // ✅ fixed import

const EvaluationItems = () => {
  return (
    <>
      <div className="row">
        <div className="col md-4 tab-50 sm-100">
          <div className="serviceSingle pb-4">
            <div className="serviceIcon">
              <img src={announcement} alt="announcement" />
            </div>
            <h3 className="yoso-h3 m-0">Make Them Call™ — Magnify Your Word-of-Mouth</h3>
            <p className="yoso-desc">
              Word-of-mouth works better when referrals can find you and call instantly.
            </p>
            <ul className="list-unstyled yoso-list yoso-list-2">
              <li>
                <i className="fa-solid fa-check"></i>Word-of-mouth is powerful—websites magnify it.
              </li>
              <li>
                <i className="fa-solid fa-check"></i>Convert referrals into direct calls, not missed chances.
              </li>
            </ul>
            <MovingButton href="#" label="" icon="fa-right-long" classes="yoso-button-2" doubleIcon={false} movingEffect={true} />
          </div>
        </div>

        <div className="col md-4 tab-50 sm-100">
          <div className="serviceSingle pb-4">
            <div className="serviceIcon">
              <img src={announcement} alt="announcement" />
            </div>
            <h3 className="yoso-h3 m-0">We Handle the Tech — You Focus on the Work</h3>
            <p className="yoso-desc">
              Hate tech? No problem—we’ll handle the website so you can stay on the job.
            </p>
            <ul className="list-unstyled yoso-list yoso-list-2">
              <li>
                <i className="fa-solid fa-check"></i>No logins, no headaches—we take care of everything.
              </li>
              <li>
                <i className="fa-solid fa-check"></i>You keep working—we keep you online and findable.
              </li>
            </ul>
            <MovingButton href="#" label="" icon="fa-right-long" classes="yoso-button-2" doubleIcon={false} movingEffect={true} />
          </div>
        </div>

        <div className="col md-4 tab-50 sm-100">
          <div className="serviceSingle pb-4">
            <div className="serviceIcon">
              <img src={announcement} alt="announcement" />
            </div>
            <h3 className="yoso-h3 m-0">Show Off the Work You’re Proud Of</h3>
            <p className="yoso-desc">
              Your craftsmanship sells—your site makes sure more people actually see it.
            </p>
            <ul className="list-unstyled yoso-list yoso-list-2">
              <li>
                <i className="fa-solid fa-check"></i>Clients trust what they can see—so show your results.
              </li>
              <li>
                <i className="fa-solid fa-check"></i>A clean site turns your finished jobs into new leads.
              </li>
            </ul>
            <MovingButton href="#" label="" icon="fa-right-long" classes="yoso-button-2" doubleIcon={false} movingEffect={true} />
          </div>
        </div>
      </div>
    </>
  );
};

export default EvaluationItems;
