import { useCallback } from "react";

export function useMovingEffect<T extends HTMLElement>() {
  const onMouseMove = useCallback((e: React.MouseEvent<T>) => {
    const target = e.currentTarget;
    const rect = target.getBoundingClientRect();
    const mouseX = e.clientX - rect.left;
    const mouseY = e.clientY - rect.top;

    target.style.top = `${mouseY - target.offsetHeight / 2}px`;
    target.style.left = `${mouseX - target.offsetWidth / 2}px`;
  }, []);

  const onMouseLeave = useCallback((e: React.MouseEvent<T>) => {
    const target = e.currentTarget;
    target.style.top = `0px`;
    target.style.left = `0px`;
  }, []);

  return { onMouseMove, onMouseLeave };
}
