import "./Button.css";
import React, { useRef } from "react";
import type { ButtonProps } from "../../types/ButtonProps";
import { useMovingEffect } from "../../hooks/useMovingEffect";

const MovingButton: React.FC<ButtonProps> = ({
  href,
  label,
  target = "_self",
  icon,
  type,
  classes = "",
  movingEffect = false,
  doubleIcon = true,
}) => {
  const anchorRef = useRef<HTMLAnchorElement>(null);
  const buttonRef = useRef<HTMLButtonElement>(null);

  const { onMouseMove, onMouseLeave } = useMovingEffect();

  const commonProps = {
    className: `${classes}`,
    onMouseMove: movingEffect ? onMouseMove : undefined,
    onMouseLeave: movingEffect ? onMouseLeave : undefined,
  };

  const iconSpan = icon ? (
    doubleIcon ? (
      <span>
        <i className={`fa-solid ${icon}`}></i>
        <i className={`fa-solid ${icon}`}></i>
      </span>
    ) : (<i className={`fa-solid ${icon}`}></i>)
  ) : null;

  return href ? (
    <a
      href={href}
      target={target}
      ref={movingEffect ? anchorRef : undefined}
      {...commonProps}
    >
      {label}
      {iconSpan}
    </a>
  ) : (
    <button
      type={type ?? "button"}
      ref={movingEffect ? buttonRef : undefined}
      {...commonProps}
    >
      {label}
      {iconSpan}
    </button>
  );
};

export { MovingButton };
