import "./Awards.css";
import { MovingButton } from "../Button/Button";
import type { AwardsItem } from "../../types/AwardsItems";
import React from 'react';

import starLight from '../../images/starLight.png';
import seoIcon from '../../images/awardIcon/seo.png';
import dataAnalysisIcon from '../../images/awardIcon/data-analysis.png';
import auditIcon from '../../images/awardIcon/audit.png';
import statementIcon from '../../images/awardIcon/statement.png';
import announcementIcon from '../../images/awardIcon/announcement.png';
import lightBulbIcon from '../../images/awardIcon/light-bulb.png';

const awardsItems: AwardsItem[] = [
  { title: "Custom-built. Just pay hosting.", link: "#" },
  { title: "Win in your zip code.", link: "#" },
  { title: "High Converting Websites", link: "#" },
  { title: "Rank higher. Get real leads.", link: "#" },
  { title: "Look legit with branded email.", link: "#" },
  { title: "Grow with ads, SEO, referrals.", link: "#" },
];

const awardIcons = [
  seoIcon,
  dataAnalysisIcon,
  auditIcon,
  statementIcon,
  announcementIcon,
  lightBulbIcon,
];

const Awards = () => {
  return (
    <section className="yoso-awards yoso-padding">
      <div className="container">
        <div className="tagline darkTagline">
          <img src={starLight} alt="Star" />
          <p>With SEO Optimization</p>
        </div>
        <h2 className="yoso-h2 text-center headingLight">
          Expect Great Things
        </h2>
        <div className="row mt-5">
          {awardsItems.map((item, index) => (
            <div className="col md-6 pe-5 sm-100 tab-50" key={index}>
              <div className="awardSingle">
                <div className="awardIcon">
                  <img src={awardIcons[index]} alt={item.title} />
                </div>
                <h4 className="yoso-h4 headingLight menuText">
                  <span className="menuTextInner">{item.title}</span>
                  <span className="duplicateText">{item.title}</span>
                </h4>
                <MovingButton
                  href={item.link}
                  label=""
                  icon="fa-arrow-right"
                  classes="yoso-button-3"
                  doubleIcon={true}
                  movingEffect={false}
                />
              </div>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
};

export default Awards;
