import React, { useEffect, useRef } from 'react';
import './Cursor.css';

const Cursor = () => {
  const cursorRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const cursor = cursorRef.current;

    const handleMouseMove = (e: MouseEvent) => {
      const { clientX, clientY } = e;
      if (cursor) {
        cursor.style.top = `${clientY - cursor.offsetHeight / 2}px`;
        cursor.style.left = `${clientX - cursor.offsetWidth / 2}px`;
      }
    };

    const handleMouseOver = (e: MouseEvent) => {
      const cursor = cursorRef.current;
      if (!cursor) return;
      const target = e.target as HTMLElement;
      const computedStyle = window.getComputedStyle(target);
      if (computedStyle.cursor === 'pointer') {
        cursor.classList.add('pointer-hover');
      } else {
        cursor.classList.remove('pointer-hover');
      }
    };

    document.addEventListener('mousemove', handleMouseMove);
    document.addEventListener('mouseover', handleMouseOver);

    return () => {
      document.removeEventListener('mousemove', handleMouseMove);
      document.removeEventListener('mouseover', handleMouseOver);
    };
  }, []);

  return <div ref={cursorRef} className="cursor"></div>;
};

export default Cursor;
