import "./FeatureSlide.css";
import React from 'react'

const FeatureSlide = () => {
  return (
    <div className="featureslide">
      <div className="marquee">
        <div className="slideInner">
          {[
            "Online Credibility",
            "Lead Generation",
            "Show Your Work",
            "SEO",
            "Facebook Marketing",
            "Google Marketing",
          ].map((text, i) => (
            <h1 className="slide" key={`1-${i}`}>
              {text}
            </h1>
          ))}

          {[
            "Online Credibility",
            "Lead Generation",
            "Show Your Work",
            "SEO",
            "Facebook Marketing",
            "Google Marketing",
          ].map((text, i) => (
            <h1 className="slide" key={`2-${i}`}>
              {text}
            </h1>
          ))}

        </div>
      </div>
    </div>
  );
};

export default FeatureSlide;
