import "./Footer.css";
import React from 'react';
import icon from '../../images/header/icon.png';
import arrow from '../../images/arrow.png'; // ✅ Imported arrow image

const Footer = () => {
  const footerLinks = [
    {
      blockTitle: "Resources",
      hrefs: ["Wiki", "About us", "Features", "Gallery"]
    },
    {
      blockTitle: "Company",
      hrefs: ["Blog", "Developers", "Our story", "Career"]
    },
    {
      blockTitle: "Help Pages",
      hrefs: ["Customer", "Press info", "Blog", "Contact"]
    }
  ];

  return (
    <footer className="yoso-footer">
      <div className="container">
        <div className="footerHeader position-relative">
          <div className="row">
            <div className="col md-6 tab-100">
              <div className="row align-center">
                <div className="col md-6 tab-50 sm-100">
                  <div className="logo">
                    <img
                      src={icon}
                      alt="Icon"
                    />
                    MTC
                  </div>
                </div>
                <div className="col md-6 tab-50 sm-100">
                  <img src={icon} alt="Review" />
                </div>
              </div>
            </div>
            <div className="col md-6 tab-100 align-self-center">
              <div className="subscribeTitle">
                <h5 className="yoso-h5 headingLight">Join <br />NewsLetter</h5>
                <form className="newsLetter">
                  <input type="email" placeholder="Your email" />
                  <button aria-label="Submit">Submit</button>
                </form>
              </div>
            </div>
          </div>
        </div>

        <div className="footerMain">
          <div className="row sm-row">
            <div className="col sm-col md-5 pb-5">
              <div className="phoneWrapper">
                <i className="fa-solid fa-phone phoneIcon"></i>
                <article className="phoneDetails">
                  Give us a call
                  <span className="d-block">
                    <a href="tel:2086962033">(208) 696-2033</a>
                  </span>
                </article>
              </div>
              <p className="yoso-desc">
                Increase efficiency and deliver better customer experiences with modern solutions from MTC Marketing.
              </p>
            </div>
            <div className="col md-7 sm-link">
              <div className="row">
                {footerLinks.map((link, index) => (
                  <div className="col md-4 tab-50" key={index}>
                    <h4 className="yoso-h4 mb-5 headingLight">{link.blockTitle}</h4>
                    <ul className="list-unstyled footerLinks">
                      {link.hrefs.map((href, i) => (
                        <li key={`${index}-${i}`}>
                          <a href="#">
                            <span className="linkWrapper">
                              <img
                                src={arrow}
                                alt="Arrow"
                              />
                              <span className="linkTxt">
                                {href}
                                <span className="linkD txtTop">{href}</span>
                                <span className="linkD txtBottom">{href}</span>
                              </span>
                            </span>
                          </a>
                        </li>
                      ))}
                    </ul>
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div>
      </div>
    </footer>
  );
};

export default Footer;
