import EvaluationItems from "./EvaluationItems";
import FeatureSlide from "./FeatureSlide";
import "./YosoServices.css";
import React from 'react';

import star from "../../images/star.png"; // ✅ Fixed path import

const YosoServices = () => {
  return (
    <section className="yoso-services">
      <FeatureSlide />
      <div className="container">
        <div id="services" className="services yoso-margin">
          <div className="tagline justify-center">
            <img src={star} alt="Star" />
            <p>With SEO Optimization</p>
          </div>
          <h2 className="yoso-h2 text-center">Expect Great Things</h2>
          <EvaluationItems />
        </div>
      </div>
    </section>
  );
};

export default YosoServices;
